import React, {useState} from "react";
import '../styles/Users.css';
import { Table, Tr } from 'styled-table-component';
import {getReceipt} from "../api";
import {Receipt} from "../pages/Receipts";
import {User} from "../pages/Users";


export class ReceiptTable extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            error: null,
            isLoaded: false,
            receipts: [],        // list of users
        };
    }

    // get a list of all users and set the state variable to store the user list
    async  fetchReceipts() {
        const data = await getReceipt();

        this.setState({ receipts: data, isLoaded: true })
    }

    componentDidMount() {
        this.fetchReceipts();
    }

    // render a HTML table with user information
    // <a href={value.id}>{value.id}</a> insert later
    render() {
        const { error, isLoaded, receipts } = this.state;
        if (error) {
            return <div>Error: {error.message}</div>;
        } else if (!isLoaded) {
            return <div>Loading...</div>;
        } else {
            return (
                <div>
                    <Table className="receipt-table">
                        <thead>
                        <tr>
                            <th scope="col">buyerID</th>
                            <th scope="col">shop</th>
                            <th scope="col">description</th>
                            <th scope="col">price</th>
                            <th scope="col">category</th>
                            <th scope="col">Update category</th>
                        </tr>
                        </thead>
                        <tbody>
                        {receipts.map(receipt => {
                            return <Tr active key={receipt._id}>
                                <td>{receipt.buyerID}</td>
                                <td>{receipt.shop}</td>
                                <td>{receipt.description}</td>
                                <td>{receipt.Price}</td>
                                <td>{receipt.category}</td>
                                <td><Receipt key={receipt._id} {...receipt} /></td>
                            </Tr>
                        })}

                        </tbody>
                    </Table>
                </div>

            );
        }
    }
}