import React from 'react';
import {Button, Form} from "react-bootstrap";
import {Component} from 'react';
//import axios from 'axios';
import  { Redirect } from 'react-router-dom';
import {login} from '../api.js'

export default class Login extends Component {
    constructor(props) {
        super(props);
        this.state = {
            path: '',
            redirectState: false
        };
    }


    render() {
        if (this.state.redirectState === true){
            return <Redirect to={this.state.path} />
        }
        return (
            <Form className={"home-form"} onSubmit={(e) => login(e, this)}>
                <Form.Group>
                    <Form.Control id={"Username"} type="user" placeholder="Enter your User ID"/>
                </Form.Group>
                <Form.Group>
                    <Form.Control id={"password"} type="password" placeholder="Enter password"/>
                    <Form.Text className="text-muted">
                        We'll never share your password with anyone else.
                    </Form.Text>
                </Form.Group>
                <Button className={"home-button"} variant="primary" type="submit">
                    Submit
                </Button>
            </Form>
        );
    }
}

