export default function UserReceipts() {
    let tempID = window.location.pathname.split("/").pop();
    if (tempID === "personalpage"){
        tempID = "";
    }
    const { loading, receipts, error } = UserReceipt(tempID);
    if (loading) {
        return <p>Loading...</p>;
    }
    if (error) {
        return <p>Something went wrong: {error.message}</p>;
    }
    // use this to make sure you are getting the right data

    // Display a list of the receipts
    return (
        <div>
            <h1>Receipts List</h1>
            <AddReceipt/>

            {receipts.map(receipt => (
            <Receipt key={receipt._id} {...receipt} /> ))}
        </div>
    );
}

function Receipt(receipt) {
    const { _id, shop, description, Price , category} = receipt;
    console.log(receipt)
    const [showUpdate, setShowUpdate] = useState(false);

    return (
        <div className={`receipt receipt-${_id}`} key={_id}>
            <div className="info">
                ({_id}) {shop} {description} {Price} {category}
                <Button className={"btn"} onClick={() => setShowUpdate(!showUpdate)}>
                    {showUpdate ? "-" : "+"}
                </Button>
            </div>
            <PersonalReceiptExtended {...receipt} showUpdate={showUpdate}/>
        </div>
    );
}

function PersonalReceiptExtended(props) {
    const {_id, category, showUpdate } = props;
    const [id_input] = useState(_id);
    const [category_input, setCategory] = useState(category);

    function onSubmit() {
        //call upate receipt function
        updateReceipt({
            _id: id_input,
            category: category_input
        });
    }

    return (
        <div className={`author-expand ${showUpdate ? "show" : ""}`}>
            <form>
                {/* TODO - add value and onChange properties to inputs */}
                <input type="text" name="category_input" value = {category_input} onChange={event => { setCategory(event.target.value);}}/>
                <Button className={"btn-warning"} onClick={onSubmit}>
                    Update
                </Button>
            </form>
            <Button className={"btn-danger"} onClick={() => deleteReceipt}>
                Delete
            </Button>
        </div>
    );
}
